div.bagrutMain {
    align-items: center;
    min-height: 66vh;
}

div.fliterDiv {
    padding: 0.4em;
    padding-bottom: 0em;
    display: flex;
    justify-content: center;
}

div.bagrutContainers {
    width: 80%;
    margin: auto;
    padding-bottom: 1.1em;
}

div#bagrutCard {
    background-color: var(--themeColorBG);
    background-image: var(--themeBG_GradientLight);
    color: var(--themeColorText);
    display: flex;
    gap: 1em;
    width: 11em;
    height: 8em;
    margin: 0.6em;
}

div#bagrutCard:hover {
    background-image: linear-gradient(180deg, var(--themeGreyAlpha), transparent);
        /* background-image: linear-gradient(0deg, #9ed6d23b, transparent); */
        /* background-image: var(--themeGradient); */
}

a.downLoad {
    padding: 0.4em 1em;
    border: 1px solid var(--themeGreyAlpha);
    background: var(--constThemeColor);
    color: var(--themeColorText_DarkMode);
}

a.downLoad:hover {
    background: var(--ThemeGPTOrangeDeep);
    color: var(--themeColorText);

}

button.yearButton {
    padding: 0.3em 0.6em;
    margin: 0em 0.2em;
}

div.carouselBagrut {
    /* padding: 1em 1.2em 2em 1.2em; */
    position: relative;
    display: flex;
    margin: auto;
}

div#bagrutInfoCarusel {
    font-size: smaller;
    place-content: stretch;
    position: relative;
    display: grid;
    grid-template-rows: 2fr 8fr 0.5fr 2fr;
    font-weight: 500;
    height: max-content;
    width: 80%;
    max-width: 30em;
    height: -webkit-fill-available;
    margin: auto;
    padding: 1em;
    background-color: var(--themeColorBG);
    background-image: var(--themeBG_GradientLight);
}

div#bagrutInfoCarusel div div p {
    display: block;
}

div#bagrutInfoCarusel div div p i {
    font-size: 0.8em;
    color: var(--themeDarkRed);
    font-style: normal;
}

div.footerBagrutInfo {
    display: flex;
    gap: 2em;
    justify-content: space-around;
    margin: 0.2em 1em;
}


div.footerBagrutInfo p {
    font-size: 0.8em;
    color: var(--themeDarkRed)
}